"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/components/auth-provider"
import { login } from "@/lib/auth"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { GraduationCap } from "lucide-react"

export default function LoginPage() {
  const router = useRouter()
  const { setUser } = useAuth()
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)

  const handleDirectSignIn = async (role: string) => {
    setError("")
    setLoading(true)
    try {
      const user = login(role, "password")
      if (user) {
        setUser(user)
        router.push("/dashboard")
      } else {
        setError("Role not found")
        setLoading(false)
      }
    } catch (err) {
      setError("An error occurred.")
      setLoading(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setLoading(true)

    try {
      const user = login(email, password)

      if (!user) {
        setError("Invalid credentials")
        setLoading(false)
        return
      }

      setUser(user)

      router.push("/dashboard")
    } catch (err) {
      setError("An error occurred. Please try again.")
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-background p-4">
      <Card className="w-full max-w-md">
        <CardHeader className="space-y-4 text-center">
          <div className="flex justify-center">
            <div className="w-12 h-12 bg-primary rounded-lg flex items-center justify-center">
              <GraduationCap className="w-7 h-7 text-primary-foreground" />
            </div>
          </div>
          <div className="space-y-2">
            <CardTitle className="text-2xl font-semibold">Demo Access</CardTitle>
            <CardDescription>Enter a role as email to sign in</CardDescription>
          </div>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            {error && (
              <Alert variant="destructive">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <div className="space-y-2">
              <Label htmlFor="email">Demo Role (Email)</Label>
              <Input
                id="email"
                type="text"
                placeholder="admin, hod, faculty, or student"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input
                id="password"
                type="password"
                placeholder="Use 'password' to sign in"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>

            <Button type="submit" className="w-full" disabled={loading}>
              {loading ? "Signing in..." : "Sign In"}
            </Button>

            <div className="mt-6 p-4 bg-muted rounded-lg space-y-2 text-sm">
              <p className="font-medium text-muted-foreground">Quick Demo Roles (Click to sign in):</p>
              <div className="grid grid-cols-2 gap-2 text-xs">
                <div
                  className="p-2 bg-background rounded border cursor-pointer hover:bg-accent transition-colors flex items-center justify-center font-medium"
                  onClick={() => handleDirectSignIn("admin")}
                >
                  Admin
                </div>
                <div
                  className="p-2 bg-background rounded border cursor-pointer hover:bg-accent transition-colors flex items-center justify-center font-medium"
                  onClick={() => handleDirectSignIn("hod")}
                >
                  HOD
                </div>
                <div
                  className="p-2 bg-background rounded border cursor-pointer hover:bg-accent transition-colors flex items-center justify-center font-medium"
                  onClick={() => handleDirectSignIn("faculty")}
                >
                  Faculty
                </div>
                <div
                  className="p-2 bg-background rounded border cursor-pointer hover:bg-accent transition-colors flex items-center justify-center font-medium"
                  onClick={() => handleDirectSignIn("student")}
                >
                  Student
                </div>
              </div>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
